<template>
<section class="center-media">
  <div class="container mx-auto p-1">
    <div class="w-full h-auto">

        <swiper :modules="modules" :slides-per-view="1" :space-between="30" navigation>

          <swiper-slide></swiper-slide>
        </swiper>
        <p class="font-bold text-sm text-start text-black">داغ ترین</p>
        <div class="w-full bg-black h-0.5 mb-1"></div>
      <div class="flex flex-row flex-wrap">
        <div v-for="value in list" :key="value.news_id">
          <div class="grid grid-row-5 grid-cols-2 gap-2">
         <div v-for="items in value" :key="items.news_id" class="relative">
             <div class="h-44">
               <div class="myImage w-10/12 h-24 mx-auto">
                 <img :src="`${items.data.data.details.news_image[0].url}`" alt="" class="w-full h-full">
               </div>
               <p class="text-black text-sm font-semibold text-start"  :style="{fontSize:size+'px'}">{{items.data.data.details.title}}</p>
               <div class="flex flex-row justify-between w-full absolute bottom-0 p-1">
                 <div><span class="font-semibold text-start text-slate-700 text-sm"  :style="{fontSize:small+'px'}">3 ساعت قبل</span></div>
                 <div class="flex flex-row">
                   <font-awesome-icon :icon="['fas','comment']" class="text-slate-700 text-sm"  :style="{fontSize:small+'px'}"></font-awesome-icon>
                   <span class="font-semibold text-start text-slate-700 text-sm"  :style="{fontSize:small+'px'}">{{items.data.data.details.news_comment_count}}</span>
                   <font-awesome-icon :icon="['fas','eye']" class="text-slate-700 text-sm"  :style="{fontSize:small+'px'}"></font-awesome-icon>
                   <span class="font-semibold text-start text-slate-700 text-sm" :style="{fontSize:small+'px'}" >{{items.data.data.details.visit}}</span>
                 </div>
               </div>
             </div>
         </div>
           </div>
         </div>

        </div>
<!--      ______-->
      <div class="mt-20">
        <p class="text-black font-bold text-start">همراه با فوتبالی</p>
        <div class="bg-black h-0.5 m-3"></div>
        <img :src="require('/src/assets/image/first.jpg')" alt="" class="w-full h-60">
        <div class="flex flex-row align-center justify-between mt-5">
          <div><img :src="require('/src/assets/image/one.jpg')" alt="" class="w-full h-8"></div>
          <div><img :src="require('/src/assets/image/two.png')" alt="" class="w-full h-8"></div>
          <div><img :src="require('/src/assets/image/three.png')" alt="" class="w-full h-8"></div>
        </div>
      </div>
<!--_______-->

    </div>
  </div>
</section>
</template>

<script>
import {FontAwesomeIcon} from "@fortawesome/vue-fontawesome";
import{onMounted,reactive,ref}from 'vue'
import {Swiper,SwiperSlide} from 'swiper/vue'
import SwiperCore,{Navigation} from "swiper";
SwiperCore.use([Navigation])
import 'swiper/css'
import 'swiper/css/navigation'
import axios from 'axios'
import {handleError} from "@/components/composable/handleError";
export default {
  name: "MediaAdd",
  components:[Swiper,SwiperSlide,FontAwesomeIcon],
  setup(){
    const size=ref(12)
    const small=ref(10)
    const list=reactive([])
    const result=onMounted(async ()=>{
     await axios.all([
       axios.get('https://api.footballi.net/api/v2/news/780968'),
       axios.get('https://api.footballi.net/api/v2/news/781012'),
       axios.get('https://api.footballi.net/api/v2/news/781011'),
       axios.get('https://api.footballi.net/api/v2/news/781066'),
       axios.get('https://api.footballi.net/api/v2/news/781010'),
       axios.get('https://api.footballi.net/api/v2/news/781090'),
       axios.get('https://api.footballi.net/api/v2/news/781009'),
       axios.get('https://api.footballi.net/api/v2/news/780998'),
       axios.get('https://api.footballi.net/api/v2/news/780947'),
       axios.get('https://api.footballi.net/api/v2/news/780994'),
     ])
     .then(axios.spread((...res)=>{
       list.push(res)
     }))
      .catch((error)=>{
       handleError(error)
      })

    })
    return{
      small,
      size,
      list,
      result,
      modules:[Navigation]
    }
  }
}
</script>

