<template>
  <div>
    <Suspense>
      <template #default>
        <PlayFrindes :list="menu"/>
      </template>
      <template #fallback>
        <LoaderComponentTwo/>
      </template>
    </Suspense>
  </div>
</template>

<script>
import{reactive,ref,onMounted} from 'vue'
import{defineAsyncComponent} from "vue";
import axios from 'axios'
import LoaderComponentTwo from "@/components/composable/LoaderComponentTwo";
const PlayFrindes=defineAsyncComponent(()=>import('@/components/main/match/PlayFrindes'))
import {handleError} from "@/components/composable/handleError";
export default {
  name: "MatchNews",
  components: {LoaderComponentTwo,PlayFrindes},
  setup(){
    const sizeText=ref(10)
    const menu=reactive([])
    const total=onMounted(async()=>{
      axios.all([
        await axios.get('https://api.footballi.net/api/v2/match/111401'),
        await axios.get('https://api.footballi.net/api/v2/match/111186'),
        await axios.get('https://api.footballi.net/api/v2/match/111187'),
        await axios.get('https://api.footballi.net/api/v2/match/111188'),
        await axios.get('https://api.footballi.net/api/v2/match/111189'),
        await axios.get('https://api.footballi.net/api/v2/match/111190'),
        await axios.get('https://api.footballi.net/api/v2/match/111191'),
        await axios.get('https://api.footballi.net/api/v2/match/111192'),
        await axios.get('https://api.footballi.net/api/v2/match/111193'),
        await axios.get('https://api.footballi.net/api/v2/match/111401'),
        await axios.get('https://api.footballi.net/api/v2/match/110836'),
        await axios.get('https://api.footballi.net/api/v2/match/110835'),
      ])
      .then(axios.spread((...response)=>{
          menu.push(response)
      }))
      .catch((error)=>{
        handleError(error)
      })
    })
    return{
     menu,total,sizeText
    }
  }
}
</script>

<style scoped>
.myScroll{
  height: auto;
  overflow-y: scroll;
  overflow-x: hidden;
}
#scroll::-webkit-scrollbar{
  width: 5px;
  background-color: #fff;
}
#scroll::-webkit-scrollbar-thumb{
  background-color: grey;
}
#scroll::-webkit-scrollbar-track{
  background-color: lightgrey;
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.5);
}
</style>