<template>
<section class="center-Newsone">
  <div class="container mx-auto p-1">
    <div class="w-full h-auto">
      <div class="flex flex-col space-y-1 ">
        <div v-for="value in list" :key="value.news_id">
          <div v-for="items in value.news" :key="items.news_id" class="h-48">
            <div class="flex flex-row algn-center justify-between w-full">
              <div class="w-7/12 h-auto">
                <slot name="title1"><h2 class="font-bold text-lg text-start">{{items.title}}</h2></slot>
                <slot name="summary1"><p class="font-semibold text-black text-sm text-start my-2">{{items.summary}}</p></slot>
                <div class="flex flex-row align-center justify-between">
                  <div><span class="font-semibold text-start text-s text-slatr-800">یک دقیقه قبل</span></div>
                  <div class="flex flex-row">
                    <font-awesome-icon :icon="['fas','comment']" class="font-semibold text-sm p-1 text-slate-800"></font-awesome-icon>
                    <slot name="comment1"><span class="font-semibold text-start text-s text-slate-800">{{items.news_comment_count}}</span></slot>
                    <font-awesome-icon :icon="['fas','eye']" class="font-semibold text-sm p-1 text-slate-800"></font-awesome-icon>
                    <slot name="eye1"><span class="font-semibold text-start text-s text-slate-800">{{items.visit}}</span></slot>
                  </div>
                </div>
              </div>
              <div class="w-3/12 h-auto">
                <div class="w-full h-40">
                  <slot name="img2"><img :src="`${items.news_image[0].url}`" alt="" class="w-full h-full"></slot>
                </div>
              </div>
            </div>
          </div>
        </div>

      </div>
    </div>
  </div>
</section>
</template>

<script>
import {onMounted,reactive} from 'vue'
import EndPoints from "@/service/EndPoints";
import {FontAwesomeIcon} from "@fortawesome/vue-fontawesome";
import {handleError} from "@/components/composable/handleError";
export default {
  name: "NewsOne",
  components:{FontAwesomeIcon},
  setup(){
    const list=reactive([])
    const result=onMounted(()=>{
      EndPoints.getNewsOne()
      .then((response)=>{
        if(response.data.success){
          list.push(response.data.data)
        }
      })
      .catch((error)=>{
       handleError(error)
      })

    })
    return{
      list,
      result,
    }
  }
}
</script>

