<template>
  <div v-for="value in list3" :key="value.news_id">
    <div v-for="items in value.news" :key="items.news_id" class="h-48">
      <div class="flex flex-row align-center justify-between w-full">
        <div class="w-7/12 h-auto">
          <h2 class="font-bold text-lg text-start">{{items.title}}</h2>
          <p class="font-semibold text-black text-sm text-start my-2">{{items.summary}}</p>
          <div class="flex flex-row align-center justify-between">
            <div><span class="font-semibold text-start text-s text-slatr-800">یک دقیقه قبل</span></div>
            <div class="flex flex-row">
              <font-awesome-icon :icon="['fas','comment']" class="font-semibold text-sm p-1 text-slate-800"></font-awesome-icon>
              <span class="font-semibold text-start text-s text-slate-800">{{items.news_comment_count}}</span>
              <font-awesome-icon :icon="['fas','eye']" class="font-semibold text-sm p-1 text-slate-800"></font-awesome-icon>
              <span class="font-semibold text-start text-s text-slate-800">{{items.visit}}</span>
            </div>
          </div>
        </div>
        <div class="w-3/12 h-auto">
          <div class="w-full h-40">
           <img :src="`${items.news_image[0].url}`" alt="" class="w-full h-full">
          </div>
        </div>
      </div>
    </div>
  </div>

</template>

<script>
import{reactive,onMounted} from 'vue'
import EndPoints from "@/service/EndPoints";
import {FontAwesomeIcon} from "@fortawesome/vue-fontawesome";
import {handleError} from "@/components/composable/handleError";
export default {
  name: "NewsThree",
  components: {FontAwesomeIcon},
  setup(){
    const list3=reactive([]);
    const sum=onMounted(()=>{
      EndPoints.getNewsThree()
      .then((response)=>{
        if(response.data.success){
          list3.push(response.data.data)
        }
      })
      .catch((error)=>{
        handleError(error)
      })

    })
    return{
      list3,
      sum,
    }
  }
}
</script>

