<template>
  <div class="grid grid-cols-5 grid-rows-6 gap-2 w-full lg:grid lg:grid-cols-5 lg:grid-rows-6 lg:gap-2 lg:w-full sm:grid sm:grid-cols-2 sm:grid-rows-3 sm:gap-3" style="height: 520px">

    <div class="rounded-md border border-1 border-white num col-span-2 row-span-3 overflow-hidden bg-white lg:col-span-2 lg:row-span-3 sm:col-span-1 sm:row-span-1">
    <Suspense>
      <template #default>
        <GridSlider  :list="slider1" my-height="176" line-font="25" my-font="15"  nums="1"/>
      </template>
      <template #fallback>
        <loader-component :my-height="fullHeight" :height-one="partHeight"></loader-component>
      </template>
    </Suspense>
    </div>

   <div class="rounded-md border border-1 border-white num col-span-3 row-span-4 overflow-hidden bg-white lg:col-span-3 lg:row-span-4 sm:col-span-1 sm:row-span-1">
    <Suspense>
      <template #default>
        <GridSlider  :list="slider1" my-height="240" line-font="30" my-font="20" nums="0"/>
      </template>
      <template #fallback>
        <loader-component :my-height="fullHeight" :height-one="partHeight"></loader-component>
      </template>
    </Suspense>
   </div>

    <div class="rounded-md border border-1 border-white num bg-white overflow-hidden col-span-2 row-span-3 lg:col-span-2 lg:row-span-3 sm:col-span-1 sm:row-span-1">
      <Suspense>
        <template #default>
          <GridSlider  :list="slider1" my-height="176" line-font="25" my-font="15"  nums="2"/>
        </template>
        <template #fallback>
          <loader-component :my-height="fullHeight" :height-one="partHeight"></loader-component>
        </template>
      </Suspense>
    </div>

    <div class="rounded-md border border-1 border-white num bg-white overflow-hidden col-span-1 row-span-2 lg:col-span-1 lg:row-span-2 sm:col-span-1 sm:row-span-1">
      <Suspense>
        <template #default>
          <GridSlider class="paragraph overflow-hidden overflow-ellipsis whitespace-nowrap"  :list="slider1" my-height="100"  nums="3"/>
        </template>
        <template #fallback>
          <loader-component :my-height="fullHeight1" my-font="10" :height-one="partHeight1"></loader-component>
        </template>
      </Suspense>
    </div>

    <div class="rounded-md border border-1 border-white num bg-white overflow-hidden col-span-1 row-span-2 lg:col-span-1 lg:row-span-2 sm:col-span-1 sm:row-span-1">
      <Suspense>
        <template #default>
          <GridSlider class="paragraph overflow-hidden overflow-ellipsis whitespace-nowrap" :list="slider1" my-height="100"  nums="4"/>
        </template>
        <template #fallback>
          <loader-component :my-height="fullHeight1" my-font="10" :height-one="partHeight1"></loader-component>
        </template>
      </Suspense>
    </div>

    <div class="rounded-md border border-1 border-white num bg-white overflow-hidden col-span-1 row-span-2 lg:col-span-1 lg:row-span-2 sm:col-span-1 sm:row-span-1">
      <Suspense>
        <template #default>
          <GridSlider class="paragraph overflow-hidden overflow-ellipsis whitespace-nowrap" :list="slider1" my-height="100"  nums="5"/>
        </template>
        <template #fallback>
          <loader-component :my-height="fullHeight1" my-font="10" :height-one="partHeight1"></loader-component>
        </template>
      </Suspense>
    </div>

  </div>
</template>

<script>
import{onMounted,reactive,ref} from "vue";
import EndPoints from "@/service/EndPoints";
import{defineAsyncComponent} from "vue";
import LoaderComponent from "@/components/composable/LoaderComponent";
const GridSlider=defineAsyncComponent(()=>import('@/components/main/GridPages/GridSlider'))
import {handleError} from "@/components/composable/handleError";
export default {
  name: "SliderNews",
  components:{LoaderComponent, GridSlider},
  inheritAttrs:false,
  setup(){
    const fullHeight=ref(140)
    const partHeight=ref(40)
    const fullHeight1=ref(80)
    const partHeight1=ref(20)
    const showSlider=ref(false)
    const slider1=reactive([])
        const total=onMounted(async ()=>{

           await EndPoints.getTransfer()
          .then((response)=>{
            if(response.data.success){
              slider1.push(response.data.data)
            }
          })
          .catch((error)=>{
          handleError(error)
          })

    })
    return{
      showSlider,
      slider1,
      total,
      fullHeight,
      partHeight,
      fullHeight1,
      partHeight1

    }
  }
}
</script>

<style scoped>
.paragraph{
  overflow: hidden;
}
</style>